// @flow
import { useQuery } from '@apollo/client';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import _isEmpty from 'lodash/isEmpty';
import React, { AbstractComponent, useEffect } from 'react';
import type { Element } from 'react';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';
import { LIST_PROPERTY } from 'graphql/queries/properties/list';
import { toastr } from 'react-redux-toastr';
import { PROPERTY_ERROR_MESSAGE } from 'shared/Messages';
import _get from 'lodash/get';
import DocumentItem from './DocumentItem';
import { Widget } from 'components/Widget';
import { withErrorBoundary } from 'components/ErrorBoundary';

const useStyles = makeStyles(theme => ({
  container: {
    minHeight: '100px',
  },
  loading: {
    border: '1px solid #E0E0E0',
    backgroundColor: theme.palette.common.white,
  },
}));

export const DocumentWidget = (): Element<'div'> => {
  const { loading, error, data } = useQuery(LIST_PROPERTY);
  const properties = _get(data, ['listProperties'], []);
  const classes = useStyles();

  useEffect(() => {
    const { message } = error || {};
    if (message) {
      toastr.error(PROPERTY_ERROR_MESSAGE.fetchFailure, message);
    }
  }, [error]);

  if (_isEmpty(properties) && !loading) {
    return;
  }

  return (
    <BlockUi
      tag="div"
      blocking={loading}
      loader={<Loader active type="ball-pulse" color="rgba(231,28,99,1)" />}
      className={clsx(classes.container, { [classes.loading]: loading })}
    >
      {properties.length > 0 && (
        <Widget title="Documents">
          {properties.map((property, key) => {
            const offer = _get(property, 'AcceptedOffer', {});
            const documents = _get(offer, 'Documents', []);
            const offerId = _get(offer, 'OfferId');
            const propertyId = _get(property, 'PropertyId');
            const address = _get(property, ['PropertyData', 'FullAddress']);
            return (
              <DocumentItem
                key={key}
                propertyId={propertyId}
                offerId={offerId}
                address={address}
                documents={documents}
              />
            );
          })}
        </Widget>
      )}
    </BlockUi>
  );
};

export default (withErrorBoundary(DocumentWidget): AbstractComponent<Props>);
