import { gql } from '@apollo/client';

const GENERATE_DOCUMENT_URL = gql`
  query generateUrl($fileId: ID!, $offerId: ID!, $propertyId: ID!) {
    getPresignedDocument(
      FileId: $fileId
      OfferId: $offerId
      PropertyId: $propertyId
    ) {
      presignedUrl
    }
  }
`;
export { GENERATE_DOCUMENT_URL };
