// @flow
import clsx from 'clsx';
import React, { AbstractComponent, Element, useEffect } from 'react';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';
import { hideIfInvalidProps } from 'utils/RenderComponent';
import { Typography, List, ListItem, Link } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import _isEmpty from 'lodash/isEmpty';
import { useLazyQuery } from '@apollo/client';
import { GENERATE_DOCUMENT_URL } from 'graphql/queries/documents/presigned';
import _get from 'lodash/get';
import { ListItemIcon } from '@material-ui/core';
import DescriptionIcon from '@material-ui/icons/Description';
import { camelcase } from 'helpers/Word';

const useStyles = makeStyles(theme => ({
  link: {
    fontFamily: theme.typography.fontFamily.avenirLT,
    display: 'flex',
    color: theme.palette.text.secondary,
  },
  list: {
    marginLeft: '0px',
  },
  listItem: {
    padding: '0px 0px 8px 0px',
  },
  listItemIcon: {
    minWidth: '35px',
    color: theme.palette.text.secondary,
  },
  address: {
    ...theme.typography.h5,
    color: theme.palette.text.secondary,
    flex: 1,
  },
  linkHighlight: {
    fontWeight: '900',
  },
}));

type Props = {
  propertyId: number,
  offerId: number,
  address: string,
  documents: Array<Object>,
};

const checkValidProps = ({ documents }) => !_isEmpty(documents);

export const DocumentItem = (props: Props): Element<typeof BlockUi> => {
  const { address, documents, offerId, propertyId } = props;
  const classes = useStyles();

  const [onClickHandler, { loading, data }] = useLazyQuery(
    GENERATE_DOCUMENT_URL,
  );

  useEffect(() => {
    const preSignedUrl = _get(data, ['getPresignedDocument', 'presignedUrl']);
    if (_isEmpty(preSignedUrl)) return;
    window.open(preSignedUrl, '_blank');
  }, [data]);

  return (
    <BlockUi
      tag="div"
      blocking={loading}
      loader={<Loader active type="ball-pulse" color="rgba(231,28,99,1)" />}
      className={clsx(classes.container, { [classes.loading]: loading })}
    >
      <Typography className={classes.address}>{address}</Typography>
      <List className={classes.list}>
        {documents.map(document => {
          const { id: fileId, documentType } = document;
          return (
            <ListItem key={fileId} className={classes.listItem}>
              <ListItemIcon className={classes.listItemIcon}>
                <DescriptionIcon />
              </ListItemIcon>
              <Link
                className={classes.link}
                href="#"
                onClick={() =>
                  onClickHandler({
                    variables: {
                      fileId,
                      offerId,
                      propertyId,
                    },
                    fetchPolicy: 'no-cache',
                  })
                }
              >
                {camelcase(documentType)}
              </Link>
            </ListItem>
          );
        })}
      </List>
    </BlockUi>
  );
};

export default (hideIfInvalidProps(checkValidProps)(
  DocumentItem,
): AbstractComponent<props>);
